.extra-products {
  width: 100%;
}
.extra-product {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
}
.charts {
  margin-top: 70px;
  display: flex;
  flex-direction: row;
  flex-wrap:wrap;
  justify-content: center;
  align-items: center;
  width: 100%;
}
.chart {
  display: flex;
  //flex:5 0 0;
  width: 50%;
  position: relative;
  height: 350px;
}
.chart-big {
  //flex:10 0 0;
  position: relative;
  height: 600px;
  width: 100%;
}
.chart-content {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
